@use "colors.module.scss";
@use "variables.module.scss";

.avatarImageButton {
  padding: 0;
  border-radius: 50%;
  object-fit: cover;
  background-color: transparent;

  img {
    max-height: 50px;
    max-width: 50px;
    display: block;

    @media all and (max-width: variables.$mobile-header-width) {
      max-height: 25px;
      max-width: 25px;
    }
  }

  border: 2px solid transparent;
  cursor: pointer;
  outline: 0; // Replacing with orange border

  &:hover,
  &:focus-visible {
      border-color: colors.$challenge-orange;
  }
}